import { getCodeBlockMeta } from 'bytemd';
import Abc from './Abc.svelte';
export default function graphviz({} = {}) {
    return {
        render(node) {
            const meta = getCodeBlockMeta(node);
            if (!meta || meta.language !== 'abc')
                return;
            return {
                component: Abc,
                props: { value: meta.value },
            };
        },
    };
}
